function fn (modeler) {
  /**
   * 创建bpmn-js的新实例后，您可以使用以下命令访问bpmn-js的任何模块
   *
   * 本示例中使用的模块是：
   *
   * * ElementFactory: 创建新的形状和连接
   * * ElementRegistry: 图表的所有形状和连接的注册表
   * * Modeling: 建模的主要模块
   *
   * 我们将使用这些模块来创建新形状，将其添加到图中，然后将其连接到现有形状
   */

  // (1) 获取模块
  const elementFactory = modeler.get('elementFactory')
  const elementRegistry = modeler.get('elementRegistry')
  const modeling = modeler.get('modeling');

  // (2) 根据 id 获取现有流程和开始事件
  const process = elementRegistry.get('Process_1')
  const startEvent = elementRegistry.get('StartEvent_1');

  // (3) 创建一个新的图表形状
  const task = elementFactory.createShape({ type: 'bpmn:Task' });

  // (4) 将新 task 添加到图中
  modeling.createShape(task, { x: 400, y: 100 }, process);

  // 您现在可以通过元素注册表访问新 task
  console.log(elementRegistry.get(task.id)); // Shape { "type": "bpmn:Task", ... }

  // (5) 连线
  modeling.connect(startEvent, task);
}

